.navbar-color {
    background-color: #202020;
}

.burguer-menu{
    display: none;
}

.responsive-menu-hamburguesa{
    display: none;
}

@media screen and (max-width: 768px) {
    .menu{
        display: none;
    }

    .responsive-menu-hamburguesa{
        display: flex;
    }


    .mi-nombre{
        display: none;
    }

    .burguer-menu{
        display: flex;
        flex-direction: column;
        padding-left: 20px;
    }

    .burguer-menu-btn {
        color: white;
    }

    .navbar-color{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
}

@media screen and (max-width: 425px) {
    .burguer-icon{
        width: 30px;
        height: 30px;
    }

    .x-icon{
        width: 30px;
        height: 30px;
    }
}
